.audio{position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.audio .elements{position:absolute;top:50%;left:0;right:0;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}.audio .elements div{display:inline-block}.audio .elements .playbutton{cursor:pointer;margin:0 10px}.audio .elements .bar{margin:0 10px;position:relative;background:#000000;width:calc(100% - 215px);height:6px;border-radius:10px;vertical-align:middle}.audio .elements .bar .progress{position:absolute;top:0;left:0;background:#808080;height:100%;border-radius:10px;display:inline-block}.audio .elements .bar .button{position:absolute;display:inline-block;height:16px;width:16px;border-radius:20px;margin-top:-5px;margin-left:-10px;background:#236f86}.audio .elements .time{margin:0 10px;width:100px;color:#777777}.audio .elements .time .currenttime{color:#000000}.audio .elements .volume{margin:0 10px;cursor:pointer}



