.mashup{position:relative;display:block;background:grey;margin:30px 50px;height:150px;border:1px solid black}.mashup img{position:absolute;top:0;left:0;height:100%;z-index:0}.mashup .info{position:relative;margin-left:200px;background:white;z-index:100;height:100%}.mashup .info .title{margin-left:-100px;font-size:25px;font-weight:bold;display:inline-block;margin-right:10px;white-space:nowrap;overflow:hidden}.mashup .info .othersongs{margin-left:-80px;font-weight:lighter;font-size:10px;margin-bottom:10px;white-space:nowrap;overflow:hidden}.mashup .info .audio{width:calc(100% - 25px);margin-right:25px;height:40px;margin-bottom:10px}.mashup .info .date{display:inline-block;font-weight:lighter;font-size:10px}.mashup .info .ratings{float:left}.mashup .info .ratings.disabled{color:#808080}.mashup .info .ratings *{display:inline-block;margin:0 10px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.mashup .info .ratings .select.up{color:#008000}.mashup .info .ratings .select.down{color:#ff0000}.mashup .info .floatright{float:right;margin-right:20px}.mashup .info .floatright *{margin:0 2px}.mashup .info .floatright .download{color:#000000;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:inline-block}.mashup .info .floatright .download:hover{color:#999999}.mashup .info .floatright .elevated{display:inline-block}.mashup .info .floatright .elevated .delete{cursor:pointer;display:inline-block}.mashup .info .floatright .elevated .delete:hover{color:#FF0000}.mashup .info .floatright .elevated .edit{cursor:pointer;display:inline-block}.mashup .info .floatright .elevated .edit:hover{color:#999999}.mashup .trap{position:absolute;top:0;left:50px;height:0;z-index:1;border-top:150px solid #ffffff;border-left:150px solid transparent}.confirm{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.confirm .head{font-weight:bolder;font-size:20px;margin-bottom:20px}.confirm .yes{cursor:pointer;background-color:#b63232;border-radius:3px;display:inline-block;padding:5px 10px;color:#ffffff;margin-right:10px}.confirm .yes:hover{background-color:#802727}.confirm .no{cursor:pointer;background-color:#000000;border-radius:3px;display:inline-block;padding:5px 10px;color:white}.confirm .no:hover{background-color:#3b3b3b}@media only screen and (max-width: 650px){.mashup{margin:20px 10px}.mashup .info .date{display:none}}@media only screen and (max-width: 480px){.mashup .trap{display:none}.mashup .info{width:100%;margin-left:0}.mashup .info .title{margin:10px 0px 0px 10px}.mashup .info .othersongs{margin:0px 0px 10px 10px}.mashup .info .audio{margin-left:25px;margin-bottom:0;width:calc(100% - 50px)}.mashup .background{display:none}}



